import styles from './Button.module.scss';

interface CancelStepButtonProps {
  onClick: () => void;
  isClickable?: boolean;
  isVisible?: boolean;
}

const CancelStepButton = ({ onClick, isClickable, isVisible }: CancelStepButtonProps) => {
  const classNames = `
    ${styles.stepsNavButtons}
    ${styles.stepsNavButtons__cancel}
    ${typeof isClickable === 'undefined' || isClickable ? styles.clickable : ''}
    ${typeof isVisible === 'undefined' || isVisible ? styles.visible : ''}
  `;

  return (
    <button className={classNames} onClick={onClick}>
      Cancel
    </button>
  );
};

export default CancelStepButton;
