import styles from './Button.module.scss';

interface TogglePillButtonProps {
  onClick?: () => void;
  children: React.ReactNode;
  isOn?: boolean;

  className?: string;
}

const TogglePillButton = ({ onClick, children, isOn, className }: TogglePillButtonProps) => {
  console.log('isOn', isOn);
  const buttonClassNames = `
    ${styles.togglePillButton}
    ${className}
    ${isOn ? styles.on : ''}
  `;
  return (
    <button className={buttonClassNames} onClick={onClick}>
      {children}
    </button>
  );
};

export default TogglePillButton;
