import styles from './Button.module.scss';

interface CapsuleButtonProps {
  onClick?: () => void;
  isSelected?: boolean;
  isFinished?: boolean;
  classNames?: string;
  children: React.ReactNode;
}

const ContainedNavButton = ({
  onClick,
  isSelected,
  isFinished,
  classNames,
  children,
}: CapsuleButtonProps) => {
  const buttonClassNames = `
    ${styles.containedNavButton}
    ${classNames}
    ${isSelected ? styles.selected : ''}
    ${isFinished ? styles.finished : ''}
    `;
  return (
    <span className={buttonClassNames} onClick={onClick}>
      {children}
    </span>
  );
};

export default ContainedNavButton;
