import { FaArrowLeftLong } from 'react-icons/fa6';
import styles from './Button.module.scss';

interface PrevStepButtonProps {
  onClick: () => void;
  isClickable?: boolean;
  isVisible?: boolean;
}

const PrevStepButton = ({ onClick, isClickable, isVisible }: PrevStepButtonProps) => {
  const classNames = `
    ${styles.stepsNavButtons}
    ${styles.stepsNavButtons__prev}
    ${typeof isClickable === 'undefined' || isClickable ? styles.clickable : ''}
    ${typeof isVisible === 'undefined' || isVisible ? styles.visible : ''}
  `;

  return (
    <button className={classNames} onClick={onClick}>
      <FaArrowLeftLong /> &nbsp; Previous
    </button>
  );
};

export default PrevStepButton;
