import styles from './Button.module.scss';

interface ContainedButtonProps {
  onClick?: () => void;
  isClickable?: boolean;
  classNames?: string;
  children: React.ReactNode;

  isSelected?: boolean;
}

const ContainedButton = ({ onClick, isClickable, classNames, children }: ContainedButtonProps) => {
  const buttonClassNames = `
      ${styles.containedButton}
      ${classNames}
      ${typeof isClickable === 'undefined' || isClickable ? styles.clickable : ''}
    `;
  return (
    <button className={buttonClassNames} onClick={onClick}>
      {children}
    </button>
  );
};

export default ContainedButton;
