import { FaArrowRightLong } from 'react-icons/fa6';
import styles from './Button.module.scss';

interface NextStepButtonProps {
  onClick: () => void;
  isClickable?: boolean;
  isVisible?: boolean;
}

const NextStepButton = ({ onClick, isClickable, isVisible }: NextStepButtonProps) => {
  const classNames = `
    ${styles.stepsNavButtons}
    ${styles.stepsNavButtons__next}
    ${typeof isClickable === 'undefined' || isClickable ? styles.clickable : ''}
    ${typeof isVisible === 'undefined' || isVisible ? styles.visible : ''}
  `;
  return (
    <button className={classNames} onClick={onClick}>
      Next Step &nbsp;
      <FaArrowRightLong />
    </button>
  );
};

export default NextStepButton;
